import React, { useState } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import AppUtils from './AppUtils';
import AppHeader from './AppHeader';
import AppFooter from './AppFooter';
import RunCalendar from './RunCalendar';
import EventPopover from './EventPopover';

export const AppContext = React.createContext(null);

function App() {
  const [calendarRef] = useState(React.createRef());
  const [distance, setDistance] = useState(AppUtils.getDefaultDistance());
  const [raceday, setRaceday] = useState(AppUtils.getDefaultRaceDay());
  const [events, setEvents] = useState(AppUtils.buildRunPlanData(raceday, distance));
  const [poAnchorEl, setPoAnchorEl] = useState(null);
  const [poContentHtml, setPoContentHtml] = useState('');
  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
    },
  });

  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <AppContext.Provider value={{
          calendarRef: calendarRef,
          distance: distance,
          setDistance: setDistance,
          raceday: raceday,
          setRaceday: setRaceday,
          events: events,
          setEvents: setEvents,
          poAnchorEl: poAnchorEl,
          setPoAnchorEl: setPoAnchorEl,
          poContentHtml: poContentHtml,
          setPoContentHtml: setPoContentHtml
        }}>
          <AppHeader />
          <RunCalendar initialDate={AppUtils.getDefaultFocusRunningDate(raceday, distance).toISODate()} />
          <EventPopover />
          <AppFooter />
        </AppContext.Provider>
      </div>
    </ThemeProvider>
  );
}

export default App;
