import React, { useContext } from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { AdapterLuxon } from '@mui/x-date-pickers/AdapterLuxon';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import Cookies from 'js-cookie';
import AppUtils from './AppUtils';
import { AppContext } from './App';

function AppHeader() {
  const { calendarRef, raceday, setRaceday, distance, setDistance, setEvents } = useContext(AppContext);

  const handleOnChangeDistance = (event) => {
    let newDistance = event.target.value;
    Cookies.set('user_distance', newDistance);
    setDistance(newDistance);
    changeRunningPlan(raceday, newDistance);

  }

  const handleOnChangeRaceday = (newRaceday) => {
    Cookies.set('user_raceday', newRaceday.toISO());
    setRaceday(newRaceday);
    changeRunningPlan(newRaceday, distance);
  };

  const changeRunningPlan = (raceday, distance) => {
    setEvents(AppUtils.buildRunPlanData(raceday, distance));
    if (calendarRef) {
      let calendarApi = calendarRef.current.getApi();
      let focusDay = AppUtils.getDefaultFocusRunningDate(raceday, distance).toISODate();
      calendarApi.gotoDate(focusDay);
    }
  };

  return (
    <div className='AppHeader'>
      <div className='header-main'>
        <img id='logo' src='/logo512.png' alt='Chạy ngay đi' />
        <h1>Chạy ngay đi</h1>
      </div>
      <div className='header-secondary'>
        <FormControl sx={{ mr: 1 }}>
          <InputLabel id="select-distance-label">Chọn cự ly</InputLabel>
          <Select
            labelId="select-distance-label"
            id="select-distance"
            value={distance}
            onChange={handleOnChangeDistance}
            autoWidth
            label="Chọn cự ly"
          >
            <MenuItem value="5km">5 km</MenuItem>
            <MenuItem value="10km">10 km</MenuItem>
            <MenuItem value="21km" selected={true}>21 km</MenuItem>
            <MenuItem value="42km">42 km</MenuItem>
          </Select>
        </FormControl>
        <LocalizationProvider dateAdapter={AdapterLuxon} adapterLocale='vi'>
          <DatePicker
            label='Chọn ngày tham dự cuộc đua'
            value={raceday}
            onChange={(newRaceday) => handleOnChangeRaceday(newRaceday)}
          />
        </LocalizationProvider>
      </div>
    </div>
  );
}

export default AppHeader;