import React, { useContext } from 'react';
import Popover from '@mui/material/Popover';
import { AppContext } from './App';

function EventPopover() {
    const { poAnchorEl, setPoAnchorEl, poContentHtml } = useContext(AppContext);

    const handleClose = () => {
        setPoAnchorEl(null);
    };

    const open = Boolean(poAnchorEl);
    const id = open ? 'simple-popover' : undefined;

    function createMarkup() {
        return { __html: poContentHtml };
    }

    return (
        <Popover
            id={id}
            open={open}
            anchorEl={poAnchorEl}
            onClose={handleClose}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
        >
            <div dangerouslySetInnerHTML={createMarkup()} />
        </Popover>
    );
}

export default EventPopover;