import { DateTime } from 'luxon';
import data5km from './data/plan5km.json';
import data10km from './data/plan10km.json';
import data21km from './data/plan21km.json';
import data42km from './data/plan42km.json';
import Cookies from 'js-cookie';

class AppUtils {
    static getDefaultRaceDay = () => {
        const userRaceday = Cookies.get('user_raceday');
        if (typeof userRaceday !== 'undefined') {
            return DateTime.fromISO(userRaceday);
        }

        return DateTime.now().setZone('Asia/Ho_Chi_Minh').plus({ days: data21km.length }).endOf('week');
    };

    static getDefaultDistance = () => {
        const userDistance = Cookies.get('user_distance');
        if (typeof userDistance !== 'undefined') {
            return userDistance;
        }

        return "21km";
    }

    static getDayAfterNumDays = (num) => DateTime.now().setZone('Asia/Ho_Chi_Minh').plus({ days: num });

    static getDayBeforeNumDays = (num) => DateTime.now().setZone('Asia/Ho_Chi_Minh').minus({ days: num });

    static getDataByDistance = (distance) => {
        let data = [];
        switch (distance) {
            case '5km':
                data = data5km;
                break;
            case '10km':
                data = data10km;
                break;
            case '21km':
                data = data21km;
                break;
            case '42km':
                data = data42km;
                break;
            default:
                data = data21km;
                break;
        }
        return data;
    }

    static getStartRunningDate = (raceday, distance, data) => {
        if (typeof data === 'undefined') {
            data = this.getDataByDistance(distance);
        }

        return raceday.minus({ days: data.length - 1 });
    }

    static getDefaultFocusRunningDate = (raceday, distance, data) => {
        let startDate = this.getStartRunningDate(raceday, distance, data);
        let today = DateTime.now();
        if (startDate < today) {
            return today;
        }
        return startDate;
    }

    static buildRunPlanData = (raceday, distance) => {
        let r = [];
        if (typeof raceday === 'undefined') {
            raceday = this.getDefaultRaceDay();
        }
        let data = this.getDataByDistance(distance);
        var day = this.getStartRunningDate(raceday, distance, data);
        for (let i in data) {
            let dayText = day.toISODate();
            let titleHtml = '<div class="cnd_item_title">'+ data[i].title +'</div>';
            if(typeof data[i]['duration'] !== 'undefined') {
                titleHtml += '<div class="cnd_item_duration">('+data[i]['duration']+')</div>';
            }
            let item = {
                id: i + 1,
                title: titleHtml,
                start: dayText,
                end: dayText
            };
            if(typeof data[i]['content'] !== 'undefined') {
                // titleHtml += '<div class="cnd_item_content">'+JSON.stringify(data[i]['content'])+'</div>';
                item.content = data[i]['content'];
            }
            r.push(item);
            day = day.plus({ days: 1 });
        }
        return r;
    }
}
export default AppUtils;