import React, { useContext } from 'react';
import { DateTime } from 'luxon';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import listPlugin from '@fullcalendar/list';
import viLocale from '@fullcalendar/core/locales/vi';
import { AppContext } from './App';

function RunCalendar(props) {
    const { calendarRef, events, setPoAnchorEl, setPoContentHtml } = useContext(AppContext);
    const initialDate = typeof props.initialDate !== 'undefined' ? props.initialDate : DateTime.now().toISODate();

    const handleEventClassNames = (args) => {
        let r = [];
        if (args.event.startStr < DateTime.now().toISODate()) {
            r.push('in-past');
        }
        return r;
    }

    const handleEventContent = (args) => {
        let el = document.createElement('div');
        el.innerHTML = args.event.title;
        let arrayOfDomNodes = [el];
        return { domNodes: arrayOfDomNodes }
    };

    const handleEventClick = (args) => {
        var html = '';
        if (typeof args.event.extendedProps.content === 'string') {
            html = '<div class="cnd_popover_content">' + args.event.extendedProps.content + '</div>';
        } else if (typeof args.event.extendedProps.content === 'object') {
            for (let i in args.event.extendedProps.content) {
                html += '<div class="cnd_popover_content">';
                html += '<div class="cnd_popover_content_t">' + i + '</div>';
                html += '<div class="cnd_popover_content_c">' + args.event.extendedProps.content[i] + '</div>';
                html += '</div>';
            }
        }
        if(html) {
            setPoContentHtml(html);
            setPoAnchorEl(args.el);
        }
    }

    return (
        <div className='run-calendar'>
            <FullCalendar
                plugins={[dayGridPlugin, listPlugin]}
                initialView='dayGridMonth'
                headerToolbar={{
                    left: 'prev,next today',
                    center: 'title',
                    right: 'dayGridMonth,listWeek'
                }}
                initialDate={initialDate}
                events={events}
                eventClassNames={handleEventClassNames}
                eventContent={handleEventContent}
                eventClick={handleEventClick}
                locale={viLocale}
                ref={calendarRef}
            />
        </div>
    );
}

export default RunCalendar;
